.spinner {
  height: 60vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  display: inline-block;
  width: 80px;
  height: 80px;
  border: 3px solid rgba(195, 195, 195, 0.6);
  border-radius: 50%;
  border-top-color: #636767;
  margin-top: 500px;
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
  }
};