@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff;
  font-family: 'Montserrat';
  font-size: 1rem;
  color: #222;
  padding-bottom: 50px;
  line-height: 1.6;
}

/* Global Styles */
:root {
  --primary-color: #17a2b8;
  --dark-color: #343a40;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
}

a {
  color: #17a2b8;
  color: var(--primary-color);
  text-decoration: none;
}

/* Mobile Styles */
@media (max-width: 700px) {
  .container {
    margin-top: 8rem;
  }

  .hide-sm {
    display: none;
  }

  /* Text Styles */
  .x-large {
    font-size: 3rem;
  }

  .large {
    font-size: 2rem;
  }

  .lead {
    font-size: 1rem;
  }
  
}



.topnav{background-color:#333;overflow:hidden;position:fixed;width:100%}.topnav a{float:left;display:block;color:#f2f2f2;text-align:center;padding:35px 40px;text-decoration:none;font-size:17px}.topnav a:hover{background-color:#ddd;color:black}.topnav a.active{background-color:#04AA6D;color:white}.topnav .icon{display:none}@media screen and (max-width: 600px){.topnav a:not(:first-child){display:none}.topnav a.icon{float:right;display:block}}@media screen and (max-width: 600px){.topnav.responsive{position:relative}.topnav.responsive a.icon{position:absolute;right:0;top:0}.topnav.responsive a{float:none;display:block;text-align:left}}.spinner{height:60vh;width:100%;display:flex;justify-content:center;align-items:center;display:inline-block;width:80px;height:80px;border:3px solid rgba(195,195,195,0.6);border-radius:50%;border-top-color:#636767;margin-top:500px;-webkit-animation:spin 1s ease-in-out infinite;animation:spin 1s ease-in-out infinite}@keyframes spin{to{-webkit-transform:rotate(360deg)}}@-webkit-keyframes spin{to{-webkit-transform:rotate(360deg)}}.homepage-paragraph{font-size:20px;margin:auto;line-height:3rem;overflow:hidden;padding:0 2rem;margin-top:3rem}.head-photo{border-radius:50%;height:150px;margin-top:10px;width:165px}.hero{background-image:url(/static/media/blueConnector.47f271a0.jpg);background-repeat:no-repeat;background-size:cover;background-position:bottom center;color:#fff;display:flex;height:100vh;justify-content:center;margin-bottom:20px;padding-top:100px;position:relative;text-align:center;z-index:-2}.hero h1{font-size:46px;margin:-20px 0 20px}.hero p{font-size:20px;letter-spacing:1px}.hero::before{background-color:rgba(0,0,0,0.6);content:'';position:absolute;height:100%;left:0;top:0;width:100%;z-index:-1}.lead{font-size:1.5rem;margin-bottom:1rem}@media (max-width: 700px){.lead{font-size:1rem}}

